$progress-bar-height: 8px;

.progress-bar-wrapper {
  flex-direction: row;
  display: flex;
  width: 100%;
  align-items: center;
  // justify-content: center;
  padding: 12px 12px 12px 12px;
  .level {
    font-family: SFProDisplay;
    font-size: 12px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.33;
    letter-spacing: normal;
    text-align: center;
    color: #1b1d20;
  }
  .progress-bar {
    margin: 0px 8px 0px 8px;
    flex: 1;
    padding-top: $progress-bar-height;
    border-radius: $progress-bar-height;
    background-color: #f2f2f2 !important;
    .bar {
      border-radius: $progress-bar-height;
      background-image: linear-gradient(94deg, #ffda7b, #d69227);
    }
  }
}

.score-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  // background: blueviolet;
  .score {
    height: 26px;
    border-radius: 13px;
    background-image: linear-gradient(101deg, #ffda7b, #d69227);
    display: flex;

    min-width: 140px;
    justify-content: center;
    align-items: center;
    margin: 12px 12px 12px 12px;

    .title {
      font-family: SFProDisplay;
      font-size: 14px;
      font-weight: 500;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.29;
      letter-spacing: normal;
      color: #fcfcfc;
      text-align: center;
    }
  }
  .sub-title {
    font-family: SFProDisplay;
    font-size: 12px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: center;
    color: #3689ff;
  }
}

.page-home {
  display: flex;
  flex-direction: column;
  background-image: url('./images/bg@3x.png');
  width: 100%;
  height: 100vh;
  background-size: 100%;
  align-items: center;
  overflow: scroll;
  .content {
    display: flex;
    flex: 1;
    margin-top: 154px;
    .title-level {
      font-family: SFProDisplay;
      font-size: 24px;
      font-weight: bold;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.25;
      letter-spacing: normal;
      text-align: center;
      color: #1b1d20;
      margin-top: 70px;
    }
  }
  > .pig-image {
    position: absolute;
    width: 200px;
    height: 200px;
    margin-top: 60px;
    // margin-bottom: -px;
  }
}

.sucess-content {
  .success-title-level {
    background: linear-gradient(97deg, #ffda7b, #d69227);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .congratulation-title {
    font-family: SFProDisplay;
    font-size: 20px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.25;
    letter-spacing: normal;
    text-align: center;
    color: #1b1d20;
    margin: 20px 20px 20px 20px;
  }
  .guide-text {
    font-family: SFProDisplay;
    font-size: 15px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.33;
    letter-spacing: normal;
    text-align: justify;
    color: #1b1d20;
    margin: 12px 12px 12px 12px;
    flex: 1;
  }
  > .button {
    align-self: stretch;
    margin: 12px 12px 12px 12px;
  }
  .button-title {
    font-family: SFProDisplay;
    font-size: 16px;
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
  }
}

.list-view {
  overflow: auto;
  width: 100%;
  flex: 1;
}

.mission-row {
  display: flex;
  padding: 12px 12px 12px 12px;
  flex-direction: row;
  width: 100%;

  // margin: 12px 12px 12px 12px;
  .body-text {
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-left: 12px;
    .title {
      font-family: SFProDisplay;
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.29;
      letter-spacing: normal;
      color: #1b1d20;
    }
    .sub-title {
      font-family: SFProDisplay;
      font-size: 12px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.33;
      letter-spacing: normal;
      color: #5f6379;
    }
  }
  .image {
    width: 32px;
    height: 32px;
  }
  .button {
    border-radius: 5px;
    height: 10px;
    background-color: #3689ff;
  }
}
.button-title {
  font-family: SFProDisplay;
  font-size: 11px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.27;
  letter-spacing: normal;
  color: #fcfcfc;
  text-transform: none;
}
.item-title {
  margin: 10px 20px 10px 20px;
  font-family: SFProDisplay;
  font-size: 15px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: normal;
  text-align: center;
  color: #1b1d20;
}
.beat-pig-image {
  width: 80px;
  height: 80px;
}
